/* src/App.css */
.App {
  text-align: center;
  margin-top: 2rem;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  
}

ul {
  list-style: none;
  padding: 0;
  border: 8px solid pink;
}

li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0.5rem 0;
  margin-left: 20.7rem;
  margin-right: 20.7rem;

}

button {
  background-color: rgb(225, 130, 184);
  color: white;
  border: none;
  padding: 0.25rem 0.5rem;
  cursor: pointer;
  
}
